// Init Scrollspy
$('body').scrollspy({ target: '.navbar', offset: 200 });

// Smooth Scrolling
$("#navbarNav a").on('click', function (event) {
  if (this.hash !== "") {
    event.preventDefault();

    const hash = this.hash;

    $('html, body').animate({
      scrollTop: $(hash).offset().top
    }, 400, function () {

      window.location.hash = hash;
    });
  }
});

// Slick Carousel
$('#partnerships .carousel').slick({
    infinite: true,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    arrows: false,
    dots: false,
    pauseOnHover: false,
    pauseOnFocus: false,
    responsive: [{
        breakpoint: 1024,
        settings: {
            slidesToShow: 3
        }
    }, {
        breakpoint: 768,
        settings: {
            slidesToShow: 2
        }
    }, {
        breakpoint: 520,
        settings: {
            slidesToShow: 1
        }
    }]
});

///// Waypoints
// Landing block
let landing_section = document.getElementById('landing-block');
let navbar = document.querySelector('.navbar');
let registerBtn = document.getElementById('navbar-register-button');
let logoIcon = landing_section.querySelector('.logo-icon');
new Waypoint({
    element: landing_section,
    offset: '-70%',
    handler: function(direction) {
        if(direction === 'down') {
            registerBtn.classList.add('fadeIn');
            registerBtn.classList.remove('fadeOut');
            $(logoIcon)
                .stop(true)
                .animate(
                { height: 0 },
                {
                    duration: 400
                });
            navbar.classList.remove('bg-transparent');

        }
    }
});
new Waypoint({
    element: landing_section,
    offset: '-50%',
    handler: function(direction) {
        if(direction === 'up') {
            registerBtn.classList.remove('fadeIn');
            registerBtn.classList.add('fadeOut');
            $(logoIcon)
                .stop(true)
                .animate(
                { height: 60 },
                {
                    duration: 400
                });
            navbar.classList.add('bg-transparent');
        }
    }
});
// Panel overview section.
let panel_section = document.getElementById('panel_overview');
let panel_section_image = panel_section.querySelector('img');
new Waypoint({
    element: panel_section,
    offset: '95%',
    handler: function(direction) {
        if(direction === 'down') {
            panel_section_image.classList.add('lightSpeedIn');

        }
        else if(direction === 'up') {
            panel_section_image.classList.remove('lightSpeedIn');
        }
    }
});

// Partnerships
let partnerships_section = document.getElementById('partnerships');
let partnerships_carousel = partnerships_section.querySelector('.carousel');
new Waypoint({
    element: partnerships_carousel,
    offset: '100%',
    handler: function(direction) {
        if(direction === 'down') {
            partnerships_carousel.classList.add('zoomIn');
        }
        else if(direction === 'up') {
            partnerships_carousel.classList.remove('zoomIn');
        }
    }
});